.shop {
  display: flex;
  flex-direction: column;

  &__title {
    align-self: center;
    margin-bottom: 12px;
  }

  &__list {
    display: flex;
    justify-content: space-around;
    list-style: none;
    gap: 16px;
    padding: 0 20px;
  }

  &__item {
    color: #828282;
    cursor: pointer ;
    text-transform: uppercase;
    padding-bottom: 10px;
    border-bottom: 2px solid transparent;
    transition: border-color 0.3s, color 0.3s;

    &:hover,
    &:active,
    &:focus {
      color: $color-dark_gray;
      border-bottom: 2px solid #000;
    }
  }

  &__products {
    display: flex;
    justify-content: center;
    margin-bottom: 18px;
  }

  &__product {
    &--tablet,
    &--desktop {
      display: none;
    }

  }

  &__image {
    width: 100%;
    height: 270px;
    object-fit: cover;
    margin-bottom: 20px;
  }

  &__product-name {
    text-transform: uppercase;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.05em;
    margin: 0 0 10px;
    padding: 0 20px;
  }

  &__product-price {
    text-decoration: none;
    color: $color-dark_gray;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.05em;
    padding: 0 20px;
  }

  &__switch {
    padding-right: 20px;
    display: flex;
    gap: 32px;
    align-self: flex-end;
    text-decoration: none;
    color: $color-dark_gray;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.05em;
    margin-bottom: 32px;
  }

  &__back {
    display: block;
    width: 28px;
    height: 28px;

    background-image: url(../images/Back.svg);
  }

  &__forvard {
    display: block;
    width: 28px;
    height: 28px;
    background-image: url(../images/next.svg);
  }

  &__button-all {
    align-self: center;
    display: block;
    @include button;
  }

  @include onTablet {
    padding: 0 28px;

    &__title {
      align-self: flex-start;
    }

    &__list {
      justify-content: flex-start;
      padding: 0;
    }

    &__products {
      justify-content: center;
      gap: 30px;
    }

    &__product {
      &--tablet {
        display: block;
      }
    }

    &__image {
      height: 340px;
    }

    &__product-name {
      padding: 0;
    }

    &__product-price {
      padding: 0;
    }
  }

  @include onDesktop {
    position: relative;
    padding: 0 0 0 150px;

    &::before {
      position: absolute;
      content: "";
      width: 100px;
      height: 1px;
      left: 28px;
      top: 230px;
      background-color: rgba(52, 79, 16, 0.5);
      transform: rotate(90deg);

    }

    &::after {
      position: absolute;
      content: "shop";
      left: 60px;
      top: 130px;
      text-transform: uppercase;
      color: rgba(52, 79, 16, 0.5);
      font-family: "Sharp Sans Display No2", sans-serif;
      font-size: 14px;
      line-height: 130%;
      letter-spacing: 0.05em;
      transform: rotate(270deg);
    }

    &__title {
      font-size: 36px;
    }

    &__products {
      justify-content: flex-start;
      gap: 20px;
      margin-bottom: 45px;
    }

    &__product {
      &--desktop {
        display: block;
      }
    }

    &__image {
      height: 500px;
      transition: transform 0.4s;

      &:hover {
        cursor: pointer;
        transform: scale(1.1);
      }
    }

    &__switch {
      gap: 32px;
      align-self: flex-start;
    }
  }
}
